@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

div#main {
  flex-direction: column;
  display: flex;
}

section {
  position: relative;
}

section.dark {
  min-height: 100vh;
  color: #fff;
  background: #00000040;
}

section.light {
  color: #000;
  background: #ffffff59;
  padding: 6rem 0;
}

section img.background {
  width: 100vmax;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 300;
}

h2 {
  text-align: center;
  margin: 0;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

h3.education {
  font-size: 1.5rem;
  font-weight: 600;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  width: 30px;
  height: 30px;
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

.contact-section {
  background-color: #f9f9f9;
  border-radius: 8px;
  margin-top: 3rem;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

.contact-form {
  max-width: 600px;
  flex-direction: column;
  margin: 0 auto;
  display: flex;
}

.contact-form label {
  margin-bottom: .5rem;
  font-weight: bold;
}

.contact-input, .contact-textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: .5rem;
  font-size: 1rem;
}

.contact-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: .5rem;
  font-size: 1rem;
}

.contact-button:hover {
  background-color: #0056b3;
}

